"use client";
import { motion } from "framer-motion";
import { Card, CardContent, CardHeader, CardTitle } from "../ui/card";
import { Button } from "../ui/button";

const projectsData = [
    {
        title: "Truth in Tales",
        description: "A web application that showcases different blogs to educate users about various topics.",
        technologies: ["HTML", "CSS", "JavaScript"],
        github: "https://github.com/RandelYumul/Truth_in_Tales",
    }, 
    {
        title: "Capybara Game",
        description: "A fun 2D game where players guide a capybara to jump over obstacles while tracking scores and increasing difficulty.",
        technologies: ["Python", "Pygame"],
        github: "https://github.com/yourusername/capybara-game",
    },
    {
        title: "VirtuoHero App",
        description: "A Flutter-based mobile app featuring a login page, user authentication, and smooth UI interactions.",
        technologies: ["Flutter", "Dart"],
        github: "https://github.com/yourusername/virtuohero",
    },
    {
        title: "Personal Portfolio",
        description: "A modern, responsive portfolio website built to showcase projects, skills, and professional experience.",
        technologies: ["React", "Next.js", "Tailwind CSS"],
        github: "https://github.com/yourusername/portfolio",
    },
];

export const Projects = () => {
  return (
    <motion.div
      id="projects"
      className="h-auto flex flex-col p-20"
      initial={{ opacity: 0, y: 20 }}
      whileInView={{ opacity: 1, y: 0 }}
      viewport={{ once: true, amount: 0.3 }}
      transition={{ duration: 1 }}
    >
      <div className="flex flex-col items-center justify-start pt-0">
        <h1 className="text-5xl font-bold mb-8 text-center">Projects</h1>
        <p className="text-lg mb-8 max-w-xl text-center">
          A selection of my personal and collaborative projects demonstrating my technical skills and development experience.
        </p>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 h-auto gap-6">
        {projectsData.map((project, index) => (
          <Card key={index} className="h-full p-6 sm:p-10 flex flex-col justify-between">
            <div>
              <CardHeader>
                <CardTitle className="font-bold text-2xl text-center">{project.title}</CardTitle>
              </CardHeader>
              <div className="mt-2 border-b-2 border-gray-300 w-full"></div>
              <CardContent className="text-justify space-y-3">
                <p className="text-base">{project.description}</p>
                <div className="flex flex-wrap gap-2 mt-2">
                  {project.technologies.map((tech, idx) => (
                    <span key={idx} className="bg-gray-200 dark:bg-gray-700 text-gray-800 dark:text-gray-200 px-3 py-1 rounded-full text-sm">
                      {tech}
                    </span>
                  ))}
                </div>
              </CardContent>
            </div>
            <div className="mt-4 text-center">
                <a href={project.github} target="_blank" rel="noopener noreferrer" className="w-full">
                    <Button className="w-full">View on GitHub</Button>
                </a>
            </div>
          </Card>
        ))}
      </div>
    </motion.div>
  );
};
